import React from 'react';
import PhoneArticle from './PhoneArticle';
import Paginator from './Paginator';

const Catalog = (props) => {
  const perPageChange = (newPerPage) => {
    addr.history.push(`/phones/pag=1by${newPerPage}`);
  }

  const { phonesList, getPhonesList, searchStr, addr, buyNowHandler, inCart, handleSearch } = props;

  getPhonesList();

  const filteredPhones = searchStr
      ? phonesList.filter(phone => phone.name.toLowerCase().includes(searchStr))
      : [...phonesList];

  const newPerPage = addr.match.params.page && +addr.match.params.page.split("by")[1];
  let perPage = newPerPage ? newPerPage : 6;
  perPage = perPage <= filteredPhones.length && perPage >= 1 ? perPage : 6;

  const pagesQtty = Math.ceil(filteredPhones.length / perPage);
  const pages = [];

  for (let i = 0; i < pagesQtty; i++) {
    pages[i] = filteredPhones.slice(i * perPage, (i + 1) * perPage);
  }

  const newcurrPage = addr.match.params.page && +addr.match.params.page.split("by")[0];
  let currPage = newcurrPage ? newcurrPage : 1;
  currPage = currPage <= pagesQtty && currPage > 0 ? currPage : 1;

  if (!pages[0]) {
    return (<></>);
  }

  return (
    <div className="phoneCatalog">
      {pages[currPage-1].map(phone => (
        <PhoneArticle
          key={phone.age}
          phone={phone}
          buyNowHandler={buyNowHandler}
          inCart={inCart}
          handleSearch={handleSearch}
        />
      ))}

      <Paginator
        currPage={currPage}
        pagesQtty={pagesQtty}
        perPage={perPage}
        articlesQtty={filteredPhones.length}
        perPageChange={perPageChange}
      />
    </div>
  );
}

export default Catalog;
